
$days: (
  "mon": 0,
  "tue": 1,
  "wed": 2,
  "thu": 3,
  "fri": 4,
  "sat": 5,
  "sun": 6,
);

body {
  margin: 0;
  .calendar {
    width: 707px;
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    & div:hover {
      background-color: pink;
      position: relative;
      bottom: 20px;
    }
    &-30 div:nth-child(31),
    &-29 div:nth-child(31),
    &-29 div:nth-child(30),
    &-28 div:nth-child(29),
    &-28 div:nth-child(30) {
      display: none;
    }
  }
}

@for $i from 1 through 31 {
  .calendar div:nth-child(#{$i}) {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0  100px;
    height: 100px;
    margin-right: 1px;
    background-color: hsl(0, 0%, 93%);
    box-sizing: border-box;
    border: 1px solid black ;
    &::after {
      content: "#{$i}";
      font-size: 30px;
      font-family: Arial, Helvetica, sans-serif;
    }
  }
}

@each $week-day, $day in $days {
  .calendar-#{$week-day} div:first-child {
    margin-left: 101px * $day;
  }
}
